* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul {
  list-style: none;
}

a {
  color: #000;
  background-color: rgba(255, 255, 255, .1);
  border: solid #000;
  border-radius: 10px;
  flex-direction: column;
  margin: 5px;
  padding: 10px;
  text-decoration: none;
  display: inline-flex;
}

a:hover {
  color: #fff;
  background-color: #000;
  border: solid #fff;
}

img {
  max-width: 100%;
  display: block;
}

button {
  margin: 20px;
}

.custom-btn {
  width: 130px;
  height: 40px;
  color: #000;
  cursor: pointer;
  background: none;
  border-radius: 50px;
  padding: 10px 25px;
  font-family: Lato, sans-serif;
  font-size: 16px;
  font-weight: 500;
  display: inline-block;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), inset -7px -7px 10px rgba(0, 0, 0, .1), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

